.dsxui-listtable {
  border-collapse: inherit;
  width: 100%;
}
.dsxui-listtable td, .dsxui-listtable th {
  text-align: left;
  word-break: break-all;
  word-wrap: break-word;
  font-size: 14px;
  padding: 12px 10px;
}
.dsxui-listtable thead th {
  color: #909399;
  font-weight: bold;
  border-bottom: 1px #EBEEF5 solid;
}
.dsxui-listtable tbody td, .dsxui-listtable tbody th {
  border-bottom: 1px #EBEEF5 solid;
  padding: 12px 8px;
}
.dsxui-listtable tfoot td, .dsxui-listtable tfoot th {
  background-color: #f5f6fa;
  border-top: 1px #e1e6eb solid;
  padding: 12px 8px;
}
.dsxui-listtable h1 {
  font-size: 18px;
}
.dsxui-listtable h2 {
  font-size: 16px;
}
.dsxui-listtable h3 {
  font-size: 14px;
}
.dsxui-listtable h4 {
  font-size: 12px;
}
.dsxui-listtable p {
  font-size: 12px;
  color: #666;
}
.dsxui-listtable .child-item-icon {
  display: inline-block;
  width: 8px;
  height: 8px;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAICAYAAADED76LAAAAAXNSR0IArs4c6QAAAB9JREFUGBljmDVr1n8GPIAJjxxYajAoYCTkC0KeYAAAlPsFpv8H+QQAAAAASUVORK5CYII=");
  background-size: 8px;
  background-repeat: no-repeat;
  margin-right: 8px;
}
.dsxui-listtable .cell-flex {
  display: flex;
  align-items: center;
}
.dsxui-listtable .action-links, .dsxui-listtable .action-links a {
  color: #409EFF;
}
.dsxui-listtable .action-links a:hover {
  text-decoration: underline;
}

/*表单表格*/
.dsxui-formtable {
  width: 100%;
  box-sizing: border-box;
}
.dsxui-formtable td, .dsxui-formtable th {
  padding: 10px 0;
  text-align: left;
  font-size: 14px;
}
.dsxui-formtable td .star, .dsxui-formtable th .star {
  color: #f40;
}
.dsxui-formtable td .tips {
  color: #9D9D9D;
  margin-top: 5px;
}
.dsxui-formtable .cell-label {
  text-align: right;
  padding-right: 10px;
  content: " ";
}
.dsxui-formtable .cell-input {
  content: " ";
}
.dsxui-formtable .cell-tips {
  color: #666;
  padding-left: 15px;
}
.dsxui-formtable li {
  padding: 5px 0;
  line-height: 1;
}
.dsxui-formtable h1 {
  font-size: 18px;
}
.dsxui-formtable h2 {
  font-size: 16px;
}
.dsxui-formtable h3 {
  font-size: 14px;
}
.dsxui-formtable h4 {
  font-size: 12px;
}

.dsxui-row {
  display: flex;
  flex-direction: row;
}
.dsxui-row .dsxui-col {
  display: inline-block;
  float: left;
  box-sizing: border-box;
}

/*===模态框===*/
.dsxui-modal {
  position: fixed;
  z-index: 1050;
  display: block;
}
.dsxui-modal .dialog {
  border-radius: 6px;
  background-clip: padding-box;
  position: relative;
  display: block;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
  background-color: #fff;
}
.dsxui-modal .dialog .dialog-header {
  padding: 15px;
  position: relative;
  border-bottom: 1px #e8e8e8 solid;
}
.dsxui-modal .dialog .dialog-header a.close {
  font-size: 22px;
  color: #666;
  position: absolute;
  top: 15px;
  right: 15px;
  margin-top: -2px;
}
.dsxui-modal .dialog .dialog-content {
  padding: 15px;
  min-width: 400px;
}
.dsxui-modal .dialog .dialog-footer {
  padding: 15px;
  border-top: 1px #e8e8e8 solid;
  text-align: right;
}
.dsxui-modal .dialog .dialog-footer .btn {
  margin: 0 3px;
}

/*===提示框===*/
.dsxui-message-box {
  display: block;
  position: fixed;
  z-index: 99999;
  border-radius: 3px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
.dsxui-message-box .message-div {
  padding: 15px 20px;
  border-radius: 3px;
  color: #fff;
  white-space: nowrap;
}
.dsxui-message-box .message-success {
  background-color: #41CC24;
}
.dsxui-message-box .message-error {
  background-color: #FF002D;
}
.dsxui-message-box .message-text {
  font-size: 15px;
  margin-left: 24px;
  line-height: 1.3;
}
.dsxui-message-box .message-warning {
  background-color: #FAA722;
}
.dsxui-message-box .message-infomation {
  background-color: #53A9C2;
}
.dsxui-message-box .message-icon {
  font-size: 18px;
  height: 18px;
  line-height: 18px;
  margin: 1px 6px 0 0;
  float: left;
  width: 20px;
  display: block;
  vertical-align: middle;
}

/*======*/
.dsxui-confirm-box {
  width: 180px;
  background: #fefefe;
  border: #d5d5d5 1px solid;
  border-radius: 3px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}
.dsxui-confirm-box dl {
  clear: both;
}
.dsxui-confirm-box dl dt {
  padding: 20px 15px 0;
  font-size: 12px;
  line-height: 1;
  margin: 0;
  text-align: center;
}
.dsxui-confirm-box dl dd {
  padding: 15px;
  clear: both;
  text-align: center;
  margin: 0;
}
.dsxui-confirm-box dl dd .btn {
  display: inline-block;
  cursor: pointer;
  height: 22px;
  line-height: 20px;
  padding: 0 10px;
  border-radius: 3px;
  font-size: 12px;
}
.dsxui-confirm-box dl dd .btn-yes {
  background: #80BF1D;
  color: #fff;
  border: 1px #80BF1D solid;
}
.dsxui-confirm-box dl dd .btn-yes:hover {
  background: #64A402;
  border: 1px #64A402 solid;
}
.dsxui-confirm-box dl dd .btn-no {
  margin-left: 10px;
  background-color: #fefefe;
  color: #333;
  border: 1px #E2E2E2 solid;
}
.dsxui-confirm-box dl dd .btn-no:hover {
  background-color: #f8f8f8;
}

.dsxui-tips-box {
  position: absolute;
  z-index: 500;
  background-color: #000;
  border-radius: 3px;
  padding: 7px 10px;
  font-size: 10px;
  line-height: 1;
  color: #fff;
}

.dsxui-tips-arrow {
  position: absolute;
  z-index: 500;
  background-image: url("/images/common/arrow-down.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 16px;
  height: 16px;
  display: block;
}

.dsxui-loading-box {
  position: absolute;
  z-index: 9999;
  background: #fff;
  border: 2px #999 solid;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 14px;
  text-align: center;
}
.dsxui-loading-box .ico {
  margin-right: 5px;
  vertical-align: -2px;
  background: url("/images/common/loading16.gif") no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.dsxui-spinner {
  position: fixed;
  z-index: 1000;
  width: 80px;
  height: 80px;
  display: block;
  border-radius: 10px;
  background-color: #f5f5f5;
  background-image: url("/images/common/spinner.gif");
  background-repeat: no-repeat;
  background-size: 70% 70%;
  background-position: center center;
}

.dsxui-toast {
  position: absolute;
  display: inline-block;
  z-index: 99999;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 8px;
  max-width: 400px;
}
.dsxui-toast .dsxui-toast-content {
  padding: 18px 30px;
  font-size: 16px;
  color: #fff;
  text-align: center;
  line-height: 1.2;
}

.dsxui-tabs-container {
  display: block;
  clear: both;
  margin: 16px 0;
  position: relative;
  border-bottom: 1px #ddd solid;
  padding: 0;
}

.dsxui-tabs {
  display: block;
  padding: 0;
  margin: 0;
}
.dsxui-tabs .tab-item {
  float: left;
  position: relative;
  margin-right: 30px;
}
.dsxui-tabs .tab-item .tab-link {
  display: block;
  font-size: 14px;
  padding: 11px 0;
  line-height: 1;
  margin-bottom: -1px;
}
.dsxui-tabs .tab-item .active {
  font-weight: bold;
  color: #DB051C;
  border-bottom: 3px #DB051C solid;
}

/*uploader*/
.dsxui-uploader {
  display: flex;
  flex-wrap: wrap;
}
.dsxui-uploader-files {
  display: flex;
}
.dsxui-uploader-files-item {
  width: 90px;
  height: 90px;
  display: block;
  margin-right: 10px;
  position: relative;
}
.dsxui-uploader-files-item .del-icon {
  position: absolute;
  top: 0;
  right: -2px;
